@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eaeaea;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.mainHeaderText {
  font-weight: 700;
  background-image: linear-gradient(90deg,#031296, #00e1e9);
  background-size: 80%;
  background-repeat: no-repeat;
  background-color: #031296;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;

}

.neumorphic-card {
  border-radius: 8px;

  font-weight: 700;
  background: #eaeaea;
  box-shadow:  6px 6px 12px #afafaf,
             -6px -6px 12px #ffffff;
  border: 0px solid rgba(255,255,255,0.20);
  padding: 8px 8px 8px 12px;
}

.neumorphic-search-bar {

  background: linear-gradient(145deg, #f0f0f0, #cacaca);
  box-shadow:  6px 6px 12px #afafaf,
             -6px -6px 12px #ffffff;
  height: 50%;
  position: relative;
  border: 0px solid rgba(255,255,255,0.20);
  padding: 8px 8px 8px 0px;
  margin: 8px;
  border-radius: 4px;  

}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

input {
  padding: 0px 0px 0px 36px;
  margin: 0px 0px 0px 0px;
}


.neumorphic-search-bar::before{
  content: "";

  position: absolute;
  left: 10px;
  top: 0;
  bottom: 0;
  width: 20px;
  background: url(/static/media/search-black.f4215dbc.svg) center / contain no-repeat;

}


.dropdown-content {
  display: none;
  margin-top: 10px;
  width: 500px;

  box-shadow:  6px 6px 12px #afafaf,
             -6px -0px 12px #ffffff;

  background: linear-gradient(145deg, #f0f0f0, #cacaca);
  position: absolute;
  z-index: 1;
}

.dropdown-content div {
  min-width: 100px;
  background: linear-gradient(145deg, #f0f0f0, #cacaca);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.neumorphic-search-bar:focus-within .dropdown-content {
  display: block;
}

::-webkit-input-placeholder {
  color: "#666666";
  font-size: 16px;

  font-weight: 700;
  font-family: "Manrope";
}

:-ms-input-placeholder {
  color: "#666666";
  font-size: 16px;

  font-weight: 700;
  font-family: "Manrope";
}

::-ms-input-placeholder {
  color: "#666666";
  font-size: 16px;

  font-weight: 700;
  font-family: "Manrope";
}

::placeholder {
  color: "#666666";
  font-size: 16px;

  font-weight: 700;
  font-family: "Manrope";
}

.neumorphic-button-unpressed {
  background: #eaeaea;

  font-weight: 700;
  box-shadow:  6px 6px 12px #afafaf,
             -6px -6px 12px #ffffff;
  border: 0px solid rgba(255,255,255,0.20);
  padding: 8px 8px 8px 12px;
  margin: 8px;
}


.neumorphic-button-pressed {
  background: #eaeaea;

  font-weight: 700;
  box-shadow: inset 2px 2px 8px #afafaf,
            inset -2px -2px 8px #ffffff;
  border: 0px solid rgba(255,255,255,0.20);
  padding: 8px 8px 8px 12px;

  margin: 8px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

